
import React from "react";
import { Select } from "antd";

const { Option } = Select;

const MonthSelect = ({ value, onChange }) => {
  return (
    <Select placeholder=" " value={value} onChange={onChange}>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
      <Option value=""></Option>
    </Select>
  );
};

export default MonthSelect;
