import React, { useEffect, useState } from "react";
import { Table, Select, Input, Button, Space } from "antd";
import { EditOutlined, DeleteOutlined } from "@ant-design/icons";
import MonthSelect from "./Select";
import axios from "axios";
const { Option } = Select;

const MonthlyTable = () => {
  const [data, setData] = useState([]);
  const [name, setName] = useState("");
  const [selectedMonth, setSelectedMonth] = useState(null);
  const [totalAmount, setTotalAmount] = useState("");
  const [autoSelectMonth, setAutoSelectMonth] = useState(false);
  const [amountAdded, setAmountAdded] = useState(false);
  const [editingKey, setEditingKey] = useState("");

  const apiBaseUrl = "https://e6e0aab3493c18b5.mokky.dev/free-credit";

  const columns = [
    {
      title: "",
      dataIndex: "index",
      key: "index",
      render: (text, record, index) => index + 1,
    },
    {
      title: "",
      dataIndex: "name",
      key: "name",
    },
    {
      title: "",
      dataIndex: "month",
      key: "month",
    },
    {
      title: "",
      dataIndex: "amount",
      key: "amount",
    },
    {
      title: "",
      key: "action",
      render: (text, record) => (
        <Space size="middle">
          <a onClick={() => handleEditRow(record)}>
            <EditOutlined />
          </a>
          <a onClick={() => handleDeleteRow(record.id)}>
            <DeleteOutlined style={{ color: "red" }} />
          </a>
        </Space>
      ),
    },
  ];

  const handleAddRow = async () => {
    if (editingKey !== "") {
      //    
      setEditingKey("");
      return;
    }

    if (name && (totalAmount || amountAdded)) {
      const monthToAdd = autoSelectMonth
        ? getNextMonth(data[data.length - 1]?.month || "")
        : selectedMonth;

      //    API
      const newData = {
        name,
        month: monthToAdd,
        amount: totalAmount,
      };

      try {
        //  API
        const response = await fetch(
          "https://e6e0aab3493c18b5.mokky.dev/free-credit",
          {
            method: "POST",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(newData),
          }
        );

        if (!response.ok) {
          //    API
          console.error("     API");
          return;
        }

        //   API ,    
        const responseData = await response.json();

        setData([
          ...data,
          {
            id: responseData.id,
            name,
            month: monthToAdd,
            amount: totalAmount,
          },
        ]);

        setName("");
        if (!amountAdded) {
          setAmountAdded(true);
        }
        if (!autoSelectMonth) {
          setAutoSelectMonth(true);
        }
      } catch (error) {
        //   ,       API
        console.error("   API:", error);
      }
    }
  };

  const handleEditRow = (record) => {
    console.log(record.id);
    setEditingKey(record.id);
    setName(record.name);
    setSelectedMonth(record.month);
    setTotalAmount(record.amount);
  };
  const handleSaveRow = async (record) => {
    try {
      const updatedData = {
        name,
        month: selectedMonth,
        amount: totalAmount,
      };

      const response = await axios.patch(
        `${apiBaseUrl}/${editingKey}`,
        updatedData
      );

      if (response.status !== 200) {
        console.error("     API");
        return;
      }

      setData((prevData) => {
        const updatedItemIndex = prevData.findIndex(
          (item) => item.id === editingKey
        );
        const updatedItems = [...prevData];
        updatedItems[updatedItemIndex] = {
          ...updatedItems[updatedItemIndex],
          name,
          month: selectedMonth,
          amount: totalAmount,
        };
        return updatedItems;
      });

      setEditingKey("");
      setName(""); //     
      setSelectedMonth(null); //      
      setTotalAmount("");
    } catch (error) {
      console.error("   API   :", error);
    }
  };

  const handleDeleteRow = async (id) => {
    console.log(id);
    try {
      //  API   
      const response = await fetch(
        `https://e6e0aab3493c18b5.mokky.dev/free-credit/${id}`,
        {
          method: "DELETE",
        }
      );

      if (!response.ok) {
        //    API
        console.error(`      ${id}  API`);
        return;
      }

      //  API  ,  ,   
      const newData = data.filter((item) => item.id !== id);
      setData(newData);
      setEditingKey("");
    } catch (error) {
      //   ,       API
      console.error("     API:", error);
    }
  };

  const getNextMonth = (currentMonth) => {
    const months = [
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
      "",
    ];
    const currentIndex = months.indexOf(currentMonth);
    const nextIndex = (currentIndex + 1) % months.length;
    return months[nextIndex];
  };

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(apiBaseUrl);
        setData(response.data);
        console.log(response.data);
      } catch (error) {
        console.error("   :", error);
      }
    };

    fetchData();
  }, [apiBaseUrl]);

  return (
    <div>
      <Space>
  <Input
    placeholder=""
    value={name}
    onChange={(e) => setName(e.target.value)}
  />
  {data.length === 0 && (
    <>
      {autoSelectMonth ? (
        <span>
          {data.length > 0
            ? getNextMonth(data[data.length - 1]?.month || "")
            : ""}
        </span>
      ) : (
        <MonthSelect
          value={selectedMonth}
          onChange={(value) => setSelectedMonth(value)}
        />
      )}
      {!amountAdded && (
        <Input
          placeholder=""
          value={totalAmount}
          onChange={(e) => setTotalAmount(e.target.value)}
        />
      )}
    </>
  )}
  <Button
    type="primary"
    onClick={editingKey !== "" ? handleSaveRow : handleAddRow}
  >
    {editingKey !== "" ? " " : ""}
  </Button>
</Space>

      <Table
        columns={columns}
        dataSource={data}
        pagination={false}
        rowKey={(record) => record.id}
        rowClassName={(record) =>
          record.key === editingKey ? "editing-row" : ""
        }
      />
    </div>
  );
};

export default MonthlyTable;
